import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Button, Typography, ButtonGroup, } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import SettingIcon from '@material-ui/icons/Settings'
import Helmet from 'react-helmet';
import SubNavBar from '../../../Components/SubNavBar';

const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
        overflow: 'hidden'
    },
    divcalc: {
        borderRadius: '12px',
        padding: '1em',
        color: '#314259'
    },
    formelems: {
        '& > *': {
            margin: '10px 0px 10px 0px',
        },
    },
    row: {
        margin: '10px'
    },
    imgcenter: {
        marginLeft: '55px',
    },
}));

export default function Binaryconverter() {
    const [state, setState] = React.useState({
        result: '1',
        resultLabel: 'Binary to Decimal',
        labelInput: 'Binary',
        isBinarytodec: true,
        inputVal: '1',
    });

    const onChangeNumbersysisbin = () => {

        if (state.isBinarytodec) {
            setState({
                ...state,
                isBinarytodec: false,
                labelInput: 'Decimal',
                resultLabel: 'Decimal to Binary'
            });
        } else {
            setState({
                ...state,
                isBinarytodec: true,
                labelInput: 'Binary',
                resultLabel: 'Binary to Decimal'
            })
        }

    }

    const onClickCalculate = () => {
        if (state.isBinarytodec) {
            setState({
                ...state,
                result: state.inputVal + ' (Binary) =<strong> ' + parseInt((state.inputVal + '').replace(/[^01]/gi, ''), 2) + ' (Decimal)</strong>'
            });
        } else {
            setState({
                ...state,
                result: state.inputVal + ' (Decimal)  =<strong> ' + parseInt(state.inputVal).toString(2) + ' (Binary) </strong>'
            });
        }
        let resultDiv = document.getElementsByClassName('resultDiv')[0];
        resultDiv.className = 'resultDiv blink_me'
        setTimeout(() => {
            resultDiv.className = 'resultDiv';
        }, 1000);
    }

    React.useEffect(() => {
        onClickCalculate();
        // eslint-disable-next-line
    }, [state.inputVal, state.isBinarytodec]);


    const onInputChange = (e) => {
        setState({
            ...state, [e.target.id]: e.target.value
        })
    }

    const classes = useStyles();
    return (
        <div className={classes.root}>
            <Helmet>
                <title>Online Binary Converter | mathcalc</title>
                <meta name="keywords" content="Mathcalc,binary to decimal , decimal to binary, binary convertion,decimal conversion" />
                <meta name="description" content="Binary to decimal conversion and vice versa" />
                <meta http-equiv="X-UA-Compatible" content="IE=edge,chrome=1"></meta>
                <meta name="viewport" content="width=device-width, initial-scale=1"></meta>
            </Helmet>
            <SubNavBar
                pageTitle="Binary Converter"
                links={[{
                    url: "/numbers/",
                    urlName: "Numbers"
                }]}
            />
            <section className="hero" data-v-23847e07>
                <div style={{ padding: '1rem 0.5rem' }}>
                    <div className="container">
                        <h1 className="subtitle is-spaced is-uppercase has-text-weight-bold">
                            ONLINE BINARY CONVERTER
                           </h1>
                        <p className="has-text-letter-spacing-wide has-text-grey">
                            Binary to decimal and decimal to binary conversion  </p>
                    </div>
                </div>
            </section>
            <div className="container" data-v-14591542>
                <div className="columns" data-v-14591542>
                    <div className="column is-6" data-v-14591542>
                        <div className="box" data-v-14591542>
                            <div className="content" data-v-14591542>
                                <Typography variant="h4" className={'text-option'}>Options</Typography>
                                <br />
                                <span><strong>Type of Conversion</strong></span><br />
                                <ButtonGroup style={{ margin: '10px 0px', display: 'flow-root' }}>
                                    <Button aria-label="Binary to Decimal"
                                        className={state.isBinarytodec ? "button is-success" : 'button'}
                                        onClick={onChangeNumbersysisbin}
                                    >
                                        Binary to Decimal
                                </Button>
                                    <Button aria-label="Decimal to Binary"
                                        className={!state.isBinarytodec ? "button is-success" : 'button'}
                                        onClick={onChangeNumbersysisbin}
                                    >
                                        Decimal to Binary
                                    </Button>
                                </ButtonGroup><br />
                                <span><strong> {state.labelInput}</strong></span><br />
                                <TextField className={classes.formelems} onChange={onInputChange}
                                    value={state.inputVal} id="inputVal" variant="outlined" type="number"></TextField><br />
                            </div>
                        </div>
                    </div>
                    <div className="column is-6" data-v-14591542>
                        <div className="box " data-v-14591542>
                            <div className="content" data-v-14591542>
                                <Typography variant="h4" className={'text-option'}>Result</Typography>
                                <div className={'resultDiv blink_me'}>
                                    <span className={'resPercentage'}>{state.resultLabel}</span><br />
                                    <span style={{ overflowWrap: 'break-word' }} dangerouslySetInnerHTML={{ __html: state.result }}></span>
                                </div>
                                <br />
                                <Button variant="contained" className={"button is-info"}
                                    startIcon={<SettingIcon />}
                                    onClick={onClickCalculate}
                                >Calculate</Button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div >
    );
}
